@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&family=Roboto&display=swap);
.container {
  margin: 10% 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  overflow: auto;
}

.top_bar {
  padding: 1% 5% 1% 10px;
  display: -webkit-flex;
  display: flex;
  height: 50px;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  position: -webkit-sticky;
  position: sticky;
  top: 0px;
  background: #eeeeee;
}
.profile_picture {
  margin-right: 5px;
}
.picture {
  width: 40px;
  height: 40px;
  object-fit: cover;
  border-radius: 50%;
}
.details {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.name {
  font-size: 0.9em;
  font-weight: 600;
}
.date {
  font-size: 0.7em;
}

.copy {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-size: 0.9em;
}
.categories {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  font-size: 0.8em;
}
.category {
  padding: 2px 10px;
  margin: 5px; 
  text-align: center;
  border-radius: 50px;
  background-color: #dddddd;
}
.content {
  margin-top: 5px;
  padding: 0 10px;
  font-weight: 300;
  font-size: 1.1em;
  line-height: 1.5;
  padding-bottom: 10px;
  }

.bottom {
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
  background: #eeeeee;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.reactions {
    -webkit-align-items: center;
            align-items: center;
    padding-left: 10px;
    width: 200px;
    font-size: 1.3em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;

    
  }
.reaction {
    cursor: pointer;
    -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
    opacity: 0.9;
    padding: 5px 10px;
    text-align: center;
    }
    .reaction:hover {
    -webkit-transform: scale(1.2) !important;
            transform: scale(1.2) !important;
    cursor: pointer;
      opacity: 0.9;
      -webkit-filter: grayscale(0%);
              filter: grayscale(0%);
      /** background: #ddebf8 !important;
       */

      
    }
    .selected {
      opacity: 1;
      -webkit-filter: grayscale(0%);
              filter: grayscale(0%);
      background-color: #f7f8fa;
padding: 5px 10px;
    text-align: center;
    cursor: pointer;

    }


.navigation {
  -webkit-flex:1 1;
          flex:1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content:space-around;
          justify-content:space-around; 
  -webkit-align-items: center; 
          align-items: center;

}
@media screen and (max-width: 600px) {
.container {
  height: -webkit-max-content;
  height: max-content;
  max-height: 100vw;
  margin: 50% 0;
  }  
.content{
font-size: 0.9em;
  line-height: 1.333;

}
.picture {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.reactions{
  font-size: 1em;
}
}

html {
  background: #007d8a;
}
body {
  margin: 0 25%;
  background: white;

  
}
@media screen and (max-width: 600px) {
  body{
    margin: 0 0;

  }
}

